<template>
  <div class="upload">
    <el-select style="margin-bottom:10px;" v-model="uploadDirectory" placeholder="请选择上传文件夹" @change="selectDir">
      <el-option
        v-for="item in directoryOption"
        :key="item"
        :label="item"
        :value="item">
      </el-option>
    </el-select>
    <el-upload
      class="upload-demo"
      :action="uploadDirectory"
      :on-preview="handlePreview"
      :on-remove="handleRemove"
      :file-list="fileList">
      <el-button size="small" type="primary">点击上传</el-button>
      <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
    </el-upload>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        uploadDirectory: '',
        directoryOption: ['小强', '小花'],
        fileList: [],
        fileList1: [
           {name: 'food.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}, 
           {name: 'food2.jpeg', url: 'https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100'}]
      };
    },
    methods: {
      handleRemove(file, fileList) {
        console.log(file, fileList);
      },
      handlePreview(file) {
        console.log(file);
      },
      selectDir(name) {
        this.uploadDirectory = `/api/uploadPic?name=${name}`
      }
    },
    created() {
      this.$http.get('/api/getAllalbumDirName').then(res => {
        this.directoryOption = res.data.message;
      }).catch(err => {
        console.log(err);
      })
    }
  }
</script>



